import React, { useState, useEffect } from "react";
import "../Style/Service.css";

function Service() {
  const [formData, setFormData] = useState({
    serviceName: "",
    quantity: "", // i t createdDate thnh quantity
    roomNumber: "",
    customerName: "",
  });

  const [services, setServices] = useState([]); // Lu danh sch dch v

  // Gi API ly danh sch dch v
  useEffect(() => {
    fetch("http://localhost:5000/api/service/getAll")
      .then((response) => {
        if (!response.ok) {
          throw new Error("Li khi gi API");
        }
        return response.json();
      })
      .then((data) => {
        setServices(data); // Lu danh sch dch v vo state
      })
      .catch((error) => {
        console.error("Li khi ly danh sch dch v:", error);
      });
  }, []);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    const requestData = {
      service_name: formData.serviceName,
      quantity: formData.quantity, // Gi s lng thay v ngy to
      room_number: formData.roomNumber,
      customer_name: formData.customerName,
    };

    try {
      const response = await fetch("http://localhost:5000/api/service/use", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(requestData),
      });

      const result = await response.json();

      if (response.ok) {
        alert("Dch v  c to thnh cng!");
        setFormData({
          serviceName: "",
          quantity: "",
          roomNumber: "",
          customerName: "",
        });
      } else {
        alert(`Li: ${result.message}`);
      }
    } catch (error) {
      console.error("Li khi gi d liu:", error);
      alert("Li khi gi d liu!");
    }
  };

  return (
    <div className='service-container'>
      <div className='service-form'>
        <h2 className='service-title'>Little Hotelier</h2>
        <p className='service-subtitle'>Front desk management system</p>

        <form onSubmit={handleSubmit}>
          {/* Chn dch v */}
          <div className='input-group'>
            <label htmlFor='serviceName'>Tn dch v</label>
            <select
              id='serviceName'
              name='serviceName'
              value={formData.serviceName}
              onChange={handleChange}
              required
            >
              <option value=''>Chn dch v</option>
              {services.map((service) => (
                <option key={service.service_id} value={service.service_name}>
                  {service.service_name}
                </option>
              ))}
            </select>
          </div>

          {/* Nhp s lng */}
          <div className='input-group'>
            <label htmlFor='quantity'>S lng</label>
            <input
              type='number'
              id='quantity'
              name='quantity'
              placeholder='Nhp s lng'
              value={formData.quantity}
              onChange={handleChange}
              min='1'
              required
            />
          </div>

          {/* Nhp s phng */}
          <div className='input-group'>
            <label htmlFor='roomNumber'>S phng</label>
            <input
              type='text'
              id='roomNumber'
              name='roomNumber'
              placeholder='Nhp s phng'
              value={formData.roomNumber}
              onChange={handleChange}
              required
            />
          </div>

          {/* Nhp tn khch hng */}
          <div className='input-group'>
            <label htmlFor='customerName'>Tn khch hng</label>
            <input
              type='text'
              id='customerName'
              name='customerName'
              placeholder='Nhp tn khch hng'
              value={formData.customerName}
              onChange={handleChange}
              required
            />
          </div>

          <button type='submit' className='service-button'>
            To dch v
          </button>
        </form>
      </div>
    </div>
  );
}

export default Service;
